#root {
    height: 100%;
}

.resume {
    padding: 20px;
    display: flex;
    flex-direction: column;
    visibility: hidden;
    animation: 1s ease-in-out 0.2s 1 forwards slideUp;
}

.resume-title {
    width: 500px;
    margin: auto;
    margin-bottom: 40px;
}

.resume-timeline {
    width: 1200px;
    position: relative;
    margin: auto;
}

.resume-timeline-marker {
    width: 50px;
    position: absolute;
    right: -30px;
    z-index: 10;
    top: 60px;
}

.resume-timeline-section {
    position: relative;
    width: 50%;
    padding: 30px 0px;
}

.resume-timeline-text {
    padding: 20px;
    width: 70%;
    margin-right: auto;
    margin-left: auto;
    background-color: #E0AFA1;
    border-radius: 5px;
}

.right-section {
    left: 50%;
    border-left: 10px solid #8A817C;
}

.left-section {
    left: 0;
    border-right: 10px solid #8A817C;
}

.right-marker {
    left: -30px;

}

.resume-timeline-date {
    font-size: 30px;
    text-shadow: 1px 1px 2px #463F3A;
    color: #F4F3ED;
    margin-bottom: 5px;
}

.resume-timeline-location {
    font-size: 20px;
    text-shadow: 1px 1px 1px #8A817C;
    color: #463F3A;
    margin-bottom: 5px;
}

.resume-timeline-description {
    font-size: 15px;
    color: #463F3A;
}

.resume-timeline-additional-information {
    display: none;
}

.resume-timeline-section:hover .resume-timeline-additional-information {
    display: inline-block;
}

.left-arrow {
    height: 0;
    width: 0;
    position: absolute;
    top: 70px;
    right: 50px;
    z-index: 1;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-left: 20px solid #E0AFA1;
}

.right-arrow {
    height: 0;
    width: 0;
    position: absolute;
    top: 70px;
    left: 50px;
    z-index: 1;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-right: 20px solid #E0AFA1;
}

.resume-timeline-rounded {
    width: 10px;
    height: 10px;
    position: absolute;
    left: 50%;
    background-color: #8A817C;
    border-radius: 100%;

}

.rounded-top {
    top: -5px;
}

.rounded-bottom {
    bottom: -5px;
}

@keyframes slideUp {
    0% {
        visibility: visible;
        opacity: 0;
        transform: translateY(15px);
    }

    100% {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
}